<style>
  main {
    margin: 0 auto 50px;
    padding-bottom: 50px;
    width: 780px;
  }

  h1 {
    text-align: center;
  }

  .btn {
    display: flex;
    justify-content: space-around;
  }

  .btn button {
    width: 120px;
  }

  .record {
    display: flex;
    box-sizing: border-box;
  }

  .card {
    margin: 20px 0 20px 30px;
    padding: 20px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px #ccc;
    /* width: 330px; */
    display: flex;
    align-items: center;
  }

  .profile {
    margin-right: 20px;
    text-align: center;
    width: 150px;
  }

  .profile p {
    font-weight: bolder;
  }

  .snsData {
    margin-left: 20px;
    width: 110px;
  }

  .twData {
    margin-bottom: 20px;
  }

  .tweet {
    margin: 25px 0 0 25px;
    height: 160px;
    overflow: hidden;
  }

  footer {
    margin-top: 50px;
    text-align: center;
  }

  #loading {
    /* width: 100vw; */
    /* height: 100vh; */
    transition: all 1s;
    background-color: #0bd;
  }
  .spinner {
    width: 100px;
    height: 100px;
    margin: 200px auto;
    background-color: #fff;
    border-radius: 100%;
    animation: sk-scaleout 1s infinite ease-in-out;
  }

  @keyframes sk-scaleout {
    0% {
      transform: scale(0);
    }
    100% {
      transform: scale(1);
      opacity: 0;
    }
  }
</style>

<script>
  import { Tweet } from 'svelte-twitter-widgets';

  // 3年生
  const memberNames = [
    // 'asuka_ugok',
    'UGOK_5563',
    'usagi_UGOK',
    'AyA3156_ugok',
    'kumi_UGOK',
    'ms_UGOK',
    'yuria_ugok',
    'misk_ugok',
    'mami_ugok',
    '0024_ugok',
    'haru_ugok',
    'natsu_ugok',
    'sakumwu',
    'HMDKN_5A',
  ];

  /*
  // 2年生
  const memberNames = [
  ];
*/

  async function getData() {
    const data = [];
    const re = /^RT\s.+:/;

    for (const member of memberNames) {
      // UGOKメンバーのTweetデータを取得
      const url = 'https://ugok-twitter-status.deno.dev/?member=' + member;
      // const url = 'http://localhost:8080/?member=' + member;
      const res = await fetch(url, {
        mode: 'cors',
      });

      if (res.ok) {
        const json = await res.json();
        if (json.status === 'success') {
          const record = json.record;

          // リアクションデータをカウント
          let nTweets = 0;
          let likes = 0;
          let retweets = 0;
          let replies = 0;
          // let quotes = 0;
          for (const tweet of record.tweets) {
            if (tweet.text.match(re)) continue; // RTはノーカウント
            nTweets += 1;
            const metrics = tweet.public_metrics;
            likes += metrics.like_count;
            retweets += metrics.retweet_count;
            // quotes += metrics.quote_count;
            replies += metrics.reply_count;
            if (tweet.in_reply_to_user_id) {
              // 自分のコメントは数えない
              if (tweet.in_reply_to_user_id === tweet.author_id) replies -= 1;
            }
          }

          // 集計データを追加
          data.push({
            userName: record.userName,
            screenName: record.screenName,
            iconUrl: record.iconUrl,
            nTweets: nTweets,
            likes: likes,
            retweets: retweets,
            replies: replies,
            // quotes: quotes,
          });
        } else {
          throw new Error(json);
        }
      } else {
        throw new Error(res);
      }
    }

    const sortedData = data.sort((a, b) => a.screenName.localeCompare(b.screenName), 'ja');

    return sortedData;
  }

  let promise = getData();
</script>

<svelte:head>
  <title>UGOK Twitter発信状況一覧サイト</title>
  <html lang="ja"></html>
  <!-- Global site tag (gtag.js) - Google Analytics -->
  <script async src="https://www.googletagmanager.com/gtag/js?id=G-1BWG4DW92L"></script>
  <script>
    window.dataLayer = window.dataLayer || [];
    function gtag() {
      dataLayer.push(arguments);
    }
    gtag('js', new Date());

    gtag('config', 'G-1BWG4DW92L');
  </script>
</svelte:head>

<main>
  {#await promise}
    <div id="loading">
      <div class="spinner"></div>
    </div>
  {:then data}
    <div id="ranking">
      <h1 style="text-align:center">
        UGOK Twitter発信状況一覧<br /><span style="font-size:small">（第1ステージ 10/4～10/24）</span>
      </h1>
      <div class="btn">
        <button
          on:click="{() => {
            data.sort((a, b) => b.nTweets - a.nTweets);
            data = data;
          }}">ツイート数</button
        >
        <button
          on:click="{() => {
            data.sort((a, b) => b.likes - a.likes);
            data = data;
          }}">いいね数</button
        >
        <button
          on:click="{() => {
            data.sort((a, b) => b.replies - a.replies);
            data = data;
          }}">コメント数</button
        >
        <button
          on:click="{() => {
            data.sort((a, b) => b.retweets - a.retweets);
            data = data;
          }}">RT数</button
        >
      </div>
      {#each data as record (record.userName)}
        <div class="record">
          <div class="card">
            <div class="profile">
              <img src="{record.iconUrl}" alt="プロフィール画像" />
              <p>{record.screenName}</p>
            </div>
            <div class="snsData">
              <div class="twData">
                &nbsp;{record.nTweets} ツイート<br />
                &nbsp;{record.likes} いいね<br />
                &nbsp;{record.replies} コメント<br />
                &nbsp;{record.retweets} RT<br />
                <!-- &nbsp;{record.quotes} 引用<br /> -->
              </div>
            </div>
          </div>
          <div class="tweet">
            <Tweet href="https://twitter.com/{record.userName}?ref_src=twsrc%5Etfw" />
            <!--
            <a
              class="twitter-timeline"
              data-tweet-limit="1"
              data-chrome="noheader nofooter"
              data-width="350"
              data-height="200"
              data-theme="light"
              href="https://twitter.com/{record.userName}?ref_src=twsrc%5Etfw">Tweets by {record.screenName}</a
            > <script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
            -->
          </div>
        </div>
      {/each}
    </div>
    <footer>&mdash; UGOK Project since 2017 &mdash;</footer>
  {:catch error}
    <p style="color: red">{error.message}</p>
  {/await}
</main>
